import React, { useContext, useState } from "react"
import ProductImage from "../ProductImage/ProductImage"
import ProductQuantityForm from "./ProductQuantityForm"
import mobile_product from '../../asset/image-product-1.jpg'
import CartContext from "../../store/cart-context"
import Column from "../Layout/Column"
import ProductDescription from "./ProductDescription"

function ProductDetail(props) {
  const ctxCart = useContext(CartContext)

  const [invalidClass, setInValidClass] = useState('')

  const handleChange = () => {
    setInValidClass('')
    props.onInvalidData({message:'Please add Some Data',showMessage:false})
  }
  const handleError = () => {
    setInValidClass('animate__shakeX')
    props.onInvalidData({message:'Please add Some Data',showMessage:true})
    props.onSuccess(false)
  }
  const handleSuccess = (Amount) => {
    setInValidClass('')
    ctxCart.addItem({
      id: props.id,
      name: props.name,
      price: props.price,
      quantity: Amount,
      productImage: props.thumbnail1.image
    })
    props.onSuccess(true)
  }

  const getQuantity = (productQuantity) => {

    productQuantity === 0 ? handleError() : handleSuccess(productQuantity)

  }
  const openModal = () => props.openModalLightBox()

  return (
    <React.Fragment>

      <Column className="col-sm-12 col-md-6 col-lg-5 p-xl-5 banner-product__image">
        <ProductImage onModal={openModal} imgHead={mobile_product} pic1={props.thumbnail1.image} pic2={props.thumbnail2.image} pic3={props.thumbnail3.image} pic4={props.thumbnail4.image} />
      </Column>

      <Column className="col-sm-12 col-md-6 col-lg-5 banner-content p-3 p-lg-5">
        <ProductDescription compay={props.company} name={props.name} description={props.description} price={props.price} discount={props.discount} mrp={props.mrp} />
        <ProductQuantityForm onChangeClass={handleChange} invalidCls={invalidClass} onGetQuantity={getQuantity} />
      </Column>


    </React.Fragment>
  )
}

export default ProductDetail